import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Gallery, Group, Header } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import './Games.css';
import axios from 'axios-jsonp-pro';
import sleep from '@react-corekit/sleep';
import FalseImg from '../img/false.png';
import Handle from '../img/handle.gif';
import TrueImg from '../img/true.png';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';



const osName = platform();




const Games = props => {

	const [fetchedfriends, setFriends] = useState([]);
	const [fetchedGroups, setGroups] = useState([]);
	const [fetchedGames, setGames] = useState([]);
	const [answer, setAnswer] = useState([]);


	useEffect(() => {

		async function fetchData() {

			const token = await bridge.send("VKWebAppGetAuthToken", { "app_id": 7401331, "scope": "friends,video, groups" });
			console.log(token);
			let urlFriends = 'https://api.vk.com/method/friends.get?user_ids=' +
				props.fetchedUser.user_id +
				'&access_token=' + props.fetchedToken +
				'&order=random' +
				'&fields=name,last_name,photo_100' +
				'&count=30' +
				'&v=5.103';

			let gameStage = [];
			let friends = await axios.jsonp(urlFriends);

			let allFriends = friends.response.items;
			let massivFriends = [];

			// удаляем друзей, у которых нет фоток и кто не заблокирован.
			allFriends.forEach(function (item, ) {

				if (item.deactivated || item.photo_100.toString().indexOf("camera_100") != -1) {}
				else massivFriends.push(item);

			});


			//console.log(token.access_token);
			let user_ids = '';
			massivFriends.forEach(function (item, ) {
				user_ids += item.id + ",";

			});
			setFriends(massivFriends);
			console.log(massivFriends);
			//console.log(user_ids);
			for (const friend of massivFriends) {

				if (gameStage.length == 5) break;

				let useFriends = false;
				for (const game of gameStage) {
					console.log(game);
					if (game.user.id == friend.id || game.friend.id == friend.id) {
						useFriends = true;
					}
				}

				if (useFriends) continue;

				let urlGroup = 'https://api.vk.com/method/groups.get?user_id=' +
					friend.id +
					'&access_token=' + props.fetchedToken +
					'&extended=1' +
					'&fiter=publics,group' +
					'&count=15' +
					'&v=5.103';
				//console.log(friend);
				let groups = await axios.jsonp(urlGroup);

				console.log(groups);
				if (groups.response) {

					let massivGroups = groups.response.items.sort(function () {
						return Math.random() - 0.5;
					});

					for (const group of massivGroups) {

						if (group.is_closed == 1) {
							continue;
						}

						let friendsToGame = null;
						await sleep(1000);
						let urlCheckGroups = 'https://api.vk.com/method/groups.isMember?user_ids=' +
							user_ids +
							'&group_id=' + group.id +
							'&access_token=' + props.fetchedToken +
							'&v=5.103';

						console.log(group);
						let isMembers = await axios.jsonp(urlCheckGroups);
						let resultMembers = [];
						if (isMembers.response) {
							resultMembers = isMembers.response.filter(function (memberGroup) {
								return memberGroup.member == 0;
							});

							if (resultMembers.length > 0) {

								// чтобы на последнем этапе не попался тот же самый пользователь.
								let randomMembers = resultMembers.sort(function () {
									return Math.random() - 0.5;
								});
								let user = massivFriends.filter(function (memberGroup) {
									return memberGroup.id == resultMembers[0].user_id;
								});
								gameStage.push(
									{ 'user': user[0], 'group': group, 'friend': friend });
								fetchedGroups.push(group);
								break;
							}


						}

					}

				}

			}
			setGames(gameStage);
			setGroups(fetchedGroups);
			console.log(gameStage);
		}
		fetchData();
	}, []);

	
	function CheckAnswer(id,i,e) {
		e.preventDefault();
		if(i < answer.length) return;

		let thisTimeGames = answer.slice();
		if (id == fetchedGames[i].friend.id) {
			thisTimeGames.push(true);
		}
		else {
			thisTimeGames.push(false);
		}
		setAnswer(thisTimeGames); 
	}

	const Round = props => {

		return (
			<div class="games_raund">
				{ /*answer.length >= i &&
					console.log("test")
				*/}
				<div><h2>Угадай кто</h2>
					<p>подписан на:</p></div>
				<div class="about_group">
					<img alt="friends" class="group_img" src={props.item.group.photo_200} />
					<p>{props.item.group.name}</p>
					<p>100 000 подписчиков</p>
				</div>

				<button class="button_answer"  onClick={(e) => CheckAnswer(props.item.friend.id,props.i,e)}>
					<div class="about_users">
						<img class="img_answer" src={''} />
						<img class="img_answer" src={''} />
						<img class="user_img" src={props.item.friend.photo_100} />
						<button  >{props.item.friend.first_name}</button>

					</div>
				</button>	

				<button class="button_answer"  onClick={(e) => CheckAnswer(props.item.user.id,props.i,e)}>
					<div class="about_users">
						<img alt="friends" class="img_answer" src={TrueImg} />
						<img alt="friends" class="img_answer" src={FalseImg} />
						<img alt="friends" class="user_img" src={props.item.user.photo_100} />
						<button >{props.item.user.first_name}</button>

					</div>
				</button>

				{props.item.friend.id === fetchedGames[fetchedGames.length - 1].friend.id &&

					<button>Закончить Игру</button>
				}
			</div>
		)
	}

	const Ready = props => {

		return(
			<div class="wait">
				<img src={Handle} />
				<h1>Ждем игру</h1>
			</div>
		)

	}

	return (
		<Panel id={props.id}>
			<Group>
			{fetchedGames.length == 0 &&
						<Ready />
			}
				<Gallery
					slideWidth="100%"
					style={{ height: "100%" }}
					className='okno'
					bullets="dark"
				>

					{fetchedGames.length > 0 &&
						fetchedGames.map((item, i) => (
							<Round 
							item = {item}
							i = {i} 
							/>
						))
					}
					


				</Gallery>
			</Group>
		</Panel>)
};

Games.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Games;


